"use strict";

import { Configuration } from "./Configuration";
import { FacetSearchResponseAdapter } from "./FacetSearchResponseAdapter";
const axios = require("axios");

export default class AnvereInstantsearchAdapter {
  constructor(options) {
    this.configuration = new Configuration(options);
    this.configuration.validate();
    this.searchClient = {
      search: (instantsearchRequests) => this.searchAnvereAndAdapt(instantsearchRequests),
      searchForFacetValues: (instantsearchRequests) =>
        this.searchAnvereForFacetValuesAndAdapt(instantsearchRequests),
    };
  }
  async adaptAndPerformAnvereRequest(instantsearchRequests) {
    const server = this.configuration.server;
    const nodes = server.nodes;
    const applicationId = nodes[0].applicationId;

    const anvereUrlWithParams = new URL(`${nodes[0].protocol}://${nodes[0].host}/docs`);
    anvereUrlWithParams.searchParams.set("app_id", applicationId);
    anvereUrlWithParams.searchParams.set("index_id", instantsearchRequests[0].indexName);
    anvereUrlWithParams.searchParams.set("input", instantsearchRequests[0].params.query);
    anvereUrlWithParams.searchParams.set("highlight", "true");
    anvereUrlWithParams.searchParams.set("page", instantsearchRequests[0].params.page);
    anvereUrlWithParams.searchParams.set("page_size", instantsearchRequests[0].params.hitsPerPage);

    const anvereResponse = await axios.get(anvereUrlWithParams.href);

    return anvereResponse.data;
  }
  async searchAnvereAndAdapt(instantsearchRequests) {
    try {
      const anvereResponse = await this.adaptAndPerformAnvereRequest(instantsearchRequests);
      return {
        results: [anvereResponse],
      };
    } catch (error) {
      throw error;
    }
  }

  async searchAnvereForFacetValuesAndAdapt(instantsearchRequests) {
    let typesenseResponse;
    try {
      typesenseResponse = await this._adaptAndPerformTypesenseRequest(instantsearchRequests);

      const adaptedResponses = typesenseResponse.results.map((typesenseResult, index) => {
        this._validateTypesenseResult(typesenseResult);
        const responseAdapter = new FacetSearchResponseAdapter(
          typesenseResult,
          instantsearchRequests[index],
          this.configuration
        );
        return responseAdapter.adapt();
      });

      return adaptedResponses;
    } catch (error) {
      console.error(error);
      throw error;
    }
  }

  _validateTypesenseResult(typesenseResult) {
    if (typesenseResult.error) {
      throw new Error(`${typesenseResult.code} - ${typesenseResult.error}`);
    }
    if (!typesenseResult.hits && !typesenseResult.grouped_hits) {
      throw new Error(`Did not find any hits. ${typesenseResult.code} - ${typesenseResult.error}`);
    }
  }
}
